@import url('https://fonts.googleapis.com/css2?family=IM+Fell+English+SC&family=Secular+One&family=Spectral:wght@600&family=UnifrakturCook:wght@700&display=swap');
h1 {
  font-family: 'UnifrakturCook', sans-serif;
}

h1.centered {
  text-align: center;
}

h2 {
  font-family: 'Spectral';
  margin-top: 5px;
  margin-bottom: 10px;
  position: relative;
  bottom: 5px;
  display: inline-flex;
}

h2.subtitle {
  color: white;
  text-align: center;
  padding: 10px;
  line-height: 40px;
  position:static;
  font-size: 20px;
  display: block;

}
.tutorial {
  line-height:20px;
}
.about {
  line-height: 20px;
  text-align: center;
}
.cover {
  max-width:40%;
  margin-left: auto;
  margin-right: auto;
  display:block;
  border: 2px solid white;
}
a {
  color:red
}
a:active {
  color:red
}
a:visited {
  color:black
}
h3 {
 text-align: left;
 font-size: 30px;
}

h4 {
  font-size:10px;
  color:black;
  margin-bottom:0px;
  text-align: center;
  font-family: 'Spectral'
}

h5 {
  font-size: 20px;
  color:white;
  font-family: 'Spectral'
}


p {
  font-family: 'Secular One';
  text-align: left;
  text-indent: 5px;
  user-select: none;
}

p.centered {
  text-align:center;
}

p.rollresult {
  text-align: center;
  font-size: 30px;
  margin: none;
  padding: none;
  display: inline-block;
  line-height: 0px;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #b12a24;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.arrowicons {
position: relative;
right:220px;
}

.hitpoints {
  padding:0px;
  display: inline-flex;
  margin:0px;
  margin-right: 30px;
  text-align:left;
  font-family: 'Secular One';
  font-size: 20px;
}
.press {
  font-family: 'Spectral';
  display: inline-block;
  background: #fcf5e5;
  border: 2px solid black;
  padding: 5px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
}

.pressdynamic {
  font-family: 'Spectral';
  display: inline-block;
  border: 2px solid black;
  padding: 5px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
}

.press:active {
  transform: scale(0.98);
}
.container {
  max-width: 500px;
  line-height: 50%;
  margin: auto;
  overflow:auto;
  padding: 20px;
  border-radius: 5px;
  background-color: #f5dca3;
  border: 2px solid black;
}

.containersmall {
  max-width:100px;
  max-height:100px;
  margin: auto;
  border-radius: 5px;
  background-color: #f5dca3;
  border: 2px solid black;
}

.combatant {
  font-family: 'IM Fell English SC';
  background: #fcf5e5;
  margin: 5px;
  padding: 5px;
  padding-left: 50px;
  padding-right: 40px;
  cursor: pointer;
  border: 2px solid black;
}

.combatant h3 {
  display:flex;
  justify-content: space-between;
  user-select: none;
}

.combatant.enemy {
  background-color: #b12a24;
  color: white;
  border-top: 3px solid black;
  border-right: 3px solid black;
  border-bottom: 3px solid black;
}

.iconstyle {
  padding-top: 30px
}

.adder {
padding: 50px;
background:#f5dca3;
color: black;
display: block;
margin:auto;
margin-bottom: 30px;
border: 3px solid black;
max-width: 300px;
}

.dicerolled{
  padding: 20px;
  background:#f5dca3;
  color: black;
  display: block;
  margin:auto;
  margin-bottom: 30px;
  border: 3px solid black;
  max-width: 300px;
}

.subadder {

}

.diceroll{
  width:55px;
}
.adder label {
  font-family: 'Spectral';
}
